<template>
  <div ref="radarChart" style="width: 50%; height: 800px;" class="myChart"></div>
</template>

<script>
import * as echarts from 'echarts';
import * as XLSX from 'xlsx'; // 引入 xlsx 库

export default {
  name: 'LeiDaTu',
  data() {
    return {
      radarChart: null,
      dataOptions: [], // 存储数据源
    };
  },
  mounted() {
    // 初始化雷达图
    this.radarChart = echarts.init(this.$refs.radarChart);
    this.initRadarChart(); // 初始化图表
  },
  computed: {
    globalday() {
      return this.$store.state.day; 
    },
    globalcities() {
      return this.$store.state.cities; 
    },
    globalFlag(){
      return this.$store.state.flag;
    }
  },
  watch: {
    globalFlag: {
      async handler(newVal, oldVal) {
        console.log("新值：", newVal);
        console.log("旧值：", oldVal);
        await this.getData(); // 等待数据加载完毕
        this.updateChart();
      },
      immediate: true, // 组件初始化时立即触发
    },
    globalday: {
      async handler(newVal, oldVal) {
        console.log("新值：", newVal);
        console.log("旧值：", oldVal);
        await this.getData(); // 等待数据加载完毕
        this.updateChart();
      },
      immediate: true, // 组件初始化时立即触发
    },
  },
  methods: {
    // 初始化图表
    async initRadarChart() {
      await this.getData(); // 等待数据加载完毕
      this.updateChart(); // 数据加载完后更新图表
    },

    // 获取数据
    async getData() {
      this.dataOptions = [];
      const promises = this.globalcities.map(city => {
        return fetch(`./data2/china_cities_2024${this.globalday.replace(/-/g, "")}_${city}_环境指数.xlsx`)
          .then(response => response.arrayBuffer())  // 读取为二进制数据
          .then(data => {
            const workbook = XLSX.read(data, { type: 'array' });
            const sheetName = workbook.SheetNames[0];
            const sheet = workbook.Sheets[sheetName];

            const jsonData = XLSX.utils.sheet_to_json(sheet, { header: 1 });
            const cityData = jsonData[1].slice(3); // 假设我们需要的是这一列数据

            // 将城市数据添加到 dataOptions 中
            this.dataOptions.push({ value: cityData, name: city });
          })
          .catch(error => console.error(`Error fetching the xlsx file for ${city}:`, error));
      });

      // 等待所有数据加载完成
      await Promise.all(promises);
    },

    // 更新图表
    updateChart() {
      const option = {
        tooltip: {},
        legend: {
          orient: 'horizontal',
          right: '0px',
          top: '100px',
          data: this.globalcities, // 动态设置图例标签
          textStyle: {
            fontSize: 16,
          },
        },
        radar: {
          center: ['50%', '45%'], // 将图表向上移动
          radius: '80%', // 增加半径占比，减少边距
          indicator: [
            { name: 'AQI', max: 200 },
            { name: 'CO', max: 2 },
            { name: 'NO2', max: 100 },
            { name: 'PM10', max: 300 },
            { name: 'PM2.5', max: 200 },
            { name: 'SO2', max: 15 },
          ],
          name: {
            textStyle: {
              fontSize: 20, // 修改所有 indicator 字体大小
            },
          },
        },
        series: [
          {
            name: '数据源对比',
            type: 'radar',
            data: this.dataOptions, // 动态设置数据
          },
        ],
      };

      // 设置图表配置
      this.radarChart.setOption(option);
    },
  },
};
</script>

<style scoped>
.myChart{
  height: 1000px;
  width: 45%;
}
</style>
