<template>
  <div class="container">
    <div class="echart" id="mychart" :style="myChartStyle"></div>
  </div>
</template>

<script>
import * as echarts from "echarts";
import * as XLSX from 'xlsx'; // 引入 xlsx 库

export default {
  data() {
    return {
      xAxisData: [],  // 存储 X 轴的数据
      myChart: null,
      seriesDatas: [],  // 存储系列数据
      myChartStyle: { width: "100%", height: "450px" }, // 图表宽度80%，高度450px
      units:{
    "气压": "hPa",
    "温度": "℃",
    "相对湿度": "%",
    "2分钟平均风速": "m/s",
    "水平能见度": "km",
    "AQI": "",
    "PM2.5": "μg/m³",
    "PM10": "μg/m³",
    "CO": "mg/m³",
    "NO2": "μg/m³",
    "SO2": "μg/m³",
  },
    };
  },
  computed: {
    globalOption3() {
      return this.$store.state.option3;
    },
    globalFlag() {
      return this.$store.state.flag;
    },
    globalDay() {
      return this.$store.state.day;
    },
    globalCities() {
      return this.$store.state.cities;
    },
    combined() {
      return {
        flag: this.$store.state.flag,
        option: this.$store.state.option3,
        day: this.$store.state.day,
      };
    },
  },
  watch: {
    combined: {
      async handler(newVal, oldVal) {
        console.log("新值：", newVal);
        console.log("旧值：", oldVal);
        this.seriesDatas = [];  // 清空原数据
        this.initDate().then(() => {
          this.$nextTick(() => {
            this.initEcharts();
          });
        });
      },
      immediate: false,  // 组件初始化时不立即触发，等数据变化时再触发
    },
  },
  mounted() {
  this.initDate().then(() => {
    this.$nextTick(() => {
      this.initEcharts();
    });
  });
  },
  methods: {
    transposeArray(arr) {
      return arr[0].map((_, colIndex) => arr.map(row => row[colIndex]));  // 转置数组
    },
    async initDate() {

      console.log(this.globalCities);
      this.seriesDatas = [];  // 清空 seriesDatas 数据
      this.xAxisData = [];  // 清空 X 轴数据
      const promises = this.globalCities.map(async (city) => {
        let filePath;
        if (["气压", "温度", "相对湿度", "2分钟平均风速", "水平能见度"].includes(this.globalOption3)) {
          filePath = `./data/combined_weather_data${this.globalDay.replace(/-/g, "")}_${city}.xlsx`;
          this.xAxisData = ['0:00', '3:00', '6:00', '9:00', '12:00', '15:00', '18:00', '21:00'];  // 定义 X 轴时间数据
        } else {
          filePath = `./data2/china_cities_2024${this.globalDay.replace(/-/g, "")}_${city}_环境指数.xlsx`;
          this.xAxisData = ['0:00', '1:00', '2:00', '3:00', '4:00', '5:00', '6:00', '7:00', '8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00'];  // 更长的时间数据
        }
        console.log(`加载数据文件: ${filePath}`);

        try {
          const response = await fetch(filePath);
          const data = await response.arrayBuffer();
          const workbook = XLSX.read(data, { type: 'array' });
          const sheetName = workbook.SheetNames[0];
          const sheet = workbook.Sheets[sheetName];
          let jsonData = XLSX.utils.sheet_to_json(sheet, { header: 1 });
          jsonData = this.transposeArray(jsonData);

          // 获取对应的数据列
          const newData = jsonData.find(element => element[0] === this.globalOption3).slice(1);
          let seriesData = [];
          newData.forEach((element, index) => {
            seriesData.push([this.xAxisData[index], element]);  // 组装数据
          });

          // 添加当前城市的数据到 seriesDatas
          this.seriesDatas.push({
            "name": city,
            "type": "line",
            "smooth": true,
            "data": newData,  // 当前城市的折线数据
          });
        } catch (error) {
          this.seriesDatas.push({
            "name": city,
            "type": "line",
            "smooth": true,
            "data": [],  // 当前城市的折线数据
          });
          console.error(`Error fetching the xlsx file for ${city}:`, error);
        }
      });

      // 等待所有城市数据加载完成
      await Promise.all(promises);
    },
    async initEcharts() {
      let maxvalue = -Infinity;
      let minvalue = Infinity;
      this.seriesDatas.forEach(element=>{
        element.data.forEach(value=>{
          if(value>maxvalue){
            maxvalue=value;
          }
          if(value<minvalue){
            minvalue=value;
          }
        });
      });

  const unit = this.units[this.globalOption3] || '';
      const optionFree = {
          xAxis: {
            type: 'category',
            data: this.xAxisData,  // 使用加载后的 X 轴数据
          },
          yAxis: {
            type: 'value',
            min: maxvalue, // Y 轴的最小值
            max: minvalue, // Y 轴的最大值
             axisLabel: {
            formatter: value => `${value} ${unit}`  // 在y轴标签上加上单位
          }
          },
          grid: { //图表位置
            top: '30px',
            left: '0px',
            right: '10px',
            bottom: '0px',
            containLabel: true, // 是否预留坐标轴宽度
          },
          series: this.seriesDatas,
        };
        // 初始化图表
        if (this.myChart === null) {
          this.myChart = echarts.init(document.getElementById("mychart"));
        }
        this.myChart.setOption(optionFree, true);
    },
  },
};
</script>

<style scoped>
.echart {
  width: 100%;  /* 使用100%宽度 */
  height: 100%;
  margin: 0 auto;  /* 居中 */
}

.container {
  margin-top: 150px;
  width: 50%;  /* 容器占满100%宽度 */
  height: 50%;
  display: flex;
  justify-content: center;  /* 保证图表在容器中居中 */
  align-items: center;  /* 垂直居中 */
  padding: 20px;  /* 添加一些内边距，避免图表紧贴容器边缘 */
  margin-left: auto;
}
</style>
