<template>
  <div class="container">
    <div class="right">
      <div ref="parallelChart" class="myChart"></div>
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import * as XLSX from 'xlsx'; // 引入 xlsx 库

export default {
  name: 'ParallelPic',
  data() {
    return {
      selectedLocation:"北京",
      chartData: [], // 存储解析后的所有数据
      filteredData: [], // 存储当前选定地点的过滤数据
      parallelChart: null, // 图表实例
    };
  },
  computed: {
    globalselectedLocation(){
      return this.$store.state.city;
    },
    globalDay(){
      return this.$store.state.day;
    }
  },
  watch:{
    globalselectedLocation: {
      async handler(newVal, oldVal) {
      console.log("新值：", newVal);
      console.log("旧值：", oldVal);
      this.selectedLocation = newVal;
      this.handleChange();
      },
    immediate: false, // 初始化时立即执行
    },
    globalDay:{
      async handler(newVal, oldVal) {
      console.log("新值：", newVal);
      console.log("旧值：", oldVal);
      this.handleChange();
      },
    immediate: false, // 初始化时立即执行
  },
  },
  mounted() {
    this.fetchAndParseCSV(); // 获取并解析 CSV 数据
    this.initParallelChart(); // 初始化图表
  },
  
  methods: {
    initParallelChart() {
      // 初始化 ECharts 实例
      this.parallelChart = echarts.init(this.$refs.parallelChart);
      
      // 默认显示所有数据
      this.updateChartData();
    },
    handleChange() {
      this.fetchAndParseCSV();
      this.updateChartData(); // 手动触发数据更新
    },

    updateChartData() {
      console.log('Selected Location:', this.selectedLocation); // 确保 location 绑定正常
      if (this.selectedLocation) {
        this.filteredData = this.extractLocationData(this.selectedLocation);
      } else {
        this.filteredData = []; // 如果没有选择地点，则不显示数据
      }

      console.log('Filtered Data:', this.filteredData); // 检查过滤后的数据

      // 渲染图表
      this.setParallelChart();
    },

    extractLocationData(location) {
      const locationData = this.chartData.filter(item => item.name === location);

      const formattedData = [];

      const hours = [...new Set(locationData.map(item => item.hour))]; // 获取所有不重复的小时
      hours.forEach(hour => {
        const hourData = { hour };

        const hourItems = locationData.filter(item => item.hour === hour);
        ['AQI', 'PM2.5', 'PM10', 'CO', 'NO2', 'SO2'].forEach(type => {
          const item = hourItems.find(i => i.type === type);
          hourData[type] = item ? item.value : 0; // 用 0 填充缺失数据
        });

        formattedData.push(hourData);
      });

      return formattedData;
    },

    fetchAndParseCSV() {
      const filePath = './assets/china_cities_2024'+this.globalDay.replace(/-/g, "")+".xlsx";
      fetch(filePath) // 使用相对路径加载 Excel 文件
        .then(response => response.arrayBuffer()) // 读取为二进制数据
        .then(data => {
          const workbook = XLSX.read(data, { type: 'array' });
          const sheetName = workbook.SheetNames[0];
          const sheet = workbook.Sheets[sheetName];

          const jsonData = XLSX.utils.sheet_to_json(sheet, { header: 1 });

          const headers = jsonData[0];
          this.locations = headers.slice(3);

          this.chartData = jsonData.slice(1).map(row => {
            const [ , hour, type, ...locationData] = row;
            return this.locations.map((location, index) => ({
              name: location,
              hour: hour,
              type: type,
              value: locationData[index],
            }));
          }).flat();

          this.updateChartData();
        })
        .catch(error => console.error('Error fetching the xlsx file:', error));
    },

    setParallelChart() {
      const pollutionTypes = ['AQI', 'PM2.5', 'PM10', 'CO', 'NO2', 'SO2'];


      const parallelAxis = [
        { dim: 0, name: 'hour' },
        ...pollutionTypes.map((type, index) => ({
          dim: index + 1,
          name: type,
        })),
        {
          dim: pollutionTypes.length + 1,
          name: 'Score',
          type: 'category',
          data: ['优', '良', '轻度污染', '中度污染', '重度污染', '严重污染'],
        },
      ];

      const seriesData = this.filteredData.map(item => [
        item.hour,
        item.AQI,
        item['PM2.5'],
        item['PM10'],
        item.CO,
        item.NO2,
        item.SO2,
        this.getScoreFromAQI(item.AQI),
      ]);

      const option = {
        
        parallelAxis: parallelAxis,
        series: {
          type: 'parallel',
          lineStyle: {
            width: 2,
            capacity: 0,
    color: "#074799",

          },
          
          data: seriesData,
          emphasis: {
        lineStyle: {
          width: 2,
          color: 'red' // 悬浮时的线条颜色
        },
        label: {
          show: true, // 悬浮时显示标签
          position: 'top', // 标签位置
        },
      },
        },
      };

      this.parallelChart.setOption(option, true);
    },

    getScoreFromAQI(AQI) {
      if (AQI <= 50) {
        return '优';
      } else if (AQI <= 100) {
        return '良';
      } else if (AQI <= 150) {
        return '轻度污染';
      } else if (AQI <= 200) {
        return '中度污染';
      } else if (AQI <= 300) {
        return '重度污染';
      } else {
        return '严重污染';
      }
    },
  },
};
</script>

<style scoped>
.container {
  display: flex;
  align-items: stretch; /* 子项拉伸对齐 */
  height: 700px; /* 确保整体高度 */
  padding: 10px;
  width: 90%;
  margin-right: 20px;
}



.right {
  width: 100%; /* 图表宽度 */
  /* background: white; */
  flex-direction: column; /* 垂直排列子元素 */
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
}

.select:focus {
  outline: none; /* 移除聚焦边框 */
  border-color: #4a90e2; /* 聚焦时边框颜色 */
  box-shadow: 0 2px 5px rgba(74, 144, 226, 0.3); /* 聚焦时阴影 */
}

.select option {
  font-size: 16px;
  padding: 8px;
}

.myChart {
  width: 100%;
  height: 100%; /* 设置高度 */
  overflow: hidden;
}
</style>


