<template>
    <div class="container">
      <CitySelect/>
      <ProvinceSelect/>
      <p class="info-text">{{ info }}</p>   
    </div>
  </template>
      
  <script>
  import CitySelect from './CitySelect';
  import ProvinceSelect from './ProvinceSelect';
  import * as XLSX from 'xlsx'; // 引入 xlsx 库
  
  export default {
    name: 'CityInfo',
    components: {
      CitySelect,
      ProvinceSelect
    },
    data() {
      return {
        info: null, // 用于显示的信息
        selectedLocation: null, // 存储选择的城市
      }
    },
    computed: {
      globalcity() {
        return this.$store.state.city; // 从 Vuex store 获取当前城市
      }
    },
    watch: {
      globalcity: {
        async handler(newVal, oldVal) {
          this.selectedLocation = newVal;  // 更新选择的城市
          await this.loadData();  // 加载数据
          console.log(oldVal);  // 打印旧值
          console.log(newVal);  // 打印新值
        },
        immediate: true,  // 在组件初始化时触发一次
      },
    },
    methods: {
      async loadData() {
        const filePath = './各省气象描述.xlsx'; // 请根据实际路径调整文件路径
  
        try {
          const response = await fetch(filePath);  // 获取 Excel 文件
          const data = await response.arrayBuffer(); // 读取文件为二进制数据
          const workbook = XLSX.read(data, { type: 'array' });
          const sheetName = workbook.SheetNames[0];
          const sheet = workbook.Sheets[sheetName];
  
          const jsonData = XLSX.utils.sheet_to_json(sheet, { header: 1 });  // 转换为二维数组
  
          // 查找与当前城市匹配的描述信息
          const cityInfo = jsonData.find(row => row[0] === this.globalcity);
          
          // 如果找到了，更新 info，否则为 null
          this.info = cityInfo ? cityInfo[1] : null;
  
        } catch (error) {
          console.error('Error loading or processing Excel file:', error);
        }
      }
    },
  }
  </script>
      
  <style>
  .container {
    display: flex;
    gap: 10px;
    justify-content: flex-start;
    width: 100%;
  }
  
  .info-text {
    top: 0; /* 对齐父容器的顶部 */
    width: 2000px; /* 设置宽度 */
    word-wrap: break-word; /* 允许文本换行 */
    font-size: 20px; /* 调整字体大小 */
    line-height: 1.5; /* 设置行高 */
    color: rgb(225, 151, 139);
    margin-bottom: 20px;
    margin-right: 30px;
  }
  </style>
  