<template>
  <div class="container">
    <div id="chartContainer"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import * as XLSX from 'xlsx'; // 引入 xlsx 库

export default {
  name: 'LineChart',
  data() {
    return {
      myChart: null,
      weatherData: [],
      xAxisData: ['0:00', '3:00', '6:00', '9:00', '12:00', '15:00', '18:00', '21:00'],
    };
  },
  computed: {
    globalcity(){
      return this.$store.state.city;
    },
    globalday(){
      return this.$store.state.day;
    },
    globaloption2(){
      return this.$store.state.option2;
    }
  },
  watch: {
    globalcity: {
      async handler(newVal, oldVal) {
        this.selectedLocation = newVal;
        this.loadData();
        console.log(oldVal);
      },
      immediate: false,
    },
    globalday: {
      async handler(newVal, oldVal) {
        this.selectedLocation = newVal;
        this.loadData();
        console.log(oldVal);
      },
      immediate: false,
    },
    globaloption2: {
      async handler(newVal, oldVal) {
        this.selectedLocation = newVal;
        this.loadData();
        console.log(oldVal);
      },
      immediate: false,
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.initChart();
      this.loadData();
    });
  },
  methods: {
    initChart() {
      const chartContainer = document.getElementById('chartContainer');
      if (!chartContainer) {
        console.error('无法获取到图表容器元素，请检查模板中是否正确定义了id为chartContainer的div元素');
        return;
      }
      this.myChart = echarts.init(chartContainer);
    },
    transposeArray(arr) {
      return arr[0].map((_, colIndex) => arr.map(row => row[colIndex]));
    },
    loadData() {
      let filePath;
      console.log(this.globalday);
      if (["气压", "温度", "相对湿度", "2分钟平均风速", "水平能见度"].includes(this.globaloption2)) {
        filePath = `./data/combined_weather_data${this.globalday.replace(/-/g, "")}_${this.globalcity}.xlsx`;
        this.xAxisData = ['0:00', '3:00', '6:00', '9:00', '12:00', '15:00', '18:00', '21:00'];
      } else {
        filePath = `./data2/china_cities_2024${this.globalday.replace(/-/g, "")}_${this.globalcity}_环境指数.xlsx`;
        this.xAxisData = ['0:00', '1:00', '2:00', '3:00', '4:00', '5:00', '6:00', '7:00', '8:00', '9:00', '10:00', '11:00', '12:00', '13:00', '14:00', '15:00', '16:00', '17:00', '18:00', '19:00', '20:00', '21:00', '22:00', '23:00'];
      }
      fetch(filePath) // 使用相对路径加载 Excel 文件
        .then(response => response.arrayBuffer()) // 读取为二进制数据
        .then(data => {
          const workbook = XLSX.read(data, { type: 'array' });
          const sheetName = workbook.SheetNames[0];
          const sheet = workbook.Sheets[sheetName];

          let jsonData = XLSX.utils.sheet_to_json(sheet, { header: 1 });

          jsonData = this.transposeArray(jsonData);
          console.log(jsonData);

          // 获取与 selectedAttribute 对应的数据
          const newData = jsonData.find(element => element[0] === this.globaloption2);
          console.log(newData);
          if (newData) {
            this.weatherData = newData.slice(1); // 只保留数据部分
          }
          console.log(this.weatherData);
          this.updateChart();
        })
        .catch(error => console.error('Error fetching the xlsx file:', error));
    },
    updateChart() {
      let max = -Infinity;
      let min = Infinity;
      this.weatherData.forEach(element=>{
        if(element>max){
          max=element;
        }
        if(element<min){
          min=element;
        }
      });
      console.log(this.weatherData);
      console.log(max);
      console.log(min);
      const units = {
    "气压": "hPa",
    "温度": "℃",
    "相对湿度": "%",
    "2分钟平均风速": "m/s",
    "水平能见度": "km",
    "AQI": "",
    "PM2.5": "μg/m³",
    "PM10": "μg/m³",
    "CO": "mg/m³",
    "NO2": "μg/m³",
    "SO2": "μg/m³",
  };
  const unit = units[this.globaloption2] || '';
      const option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: { // 可选：增强悬浮指示
            type: 'cross',
            label: {
              backgroundColor: '#6a7985',
            },
          },
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.xAxisData, // 每小时数据
        },
        yAxis: {
          type: 'value',
          min: Math.floor(0.95*min),
          max: Math.ceil(1.05*max),
          axisLabel: {
            formatter: `{value} ${unit}` // 在y轴标签上加上单位
      }
        },
        series: [{name: this.globaloption2,
          type: 'line',
          data: this.weatherData}],
      };

      this.myChart.setOption(option);
    }
  },
};
</script>


<style>
#chartContainer {
  width: 100%;
  height: 600px;
  margin-left: 20px;
}
.option{
 
  display: flex;
}
.container{
  width: 50%;
  height: 100%; /* 设置高度 */
}
.select{
  height: 50px;
  background-color: #F9DADA;
  font-size: 18px;
  margin-left: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin-bottom: 10px;
}
</style>