<template>
  <div class="container" >
    <div ref="mapChart" style="width: 80%; height: 1000px;" class="right">
    </div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import chinaJson from './100000.geo.json'; 
import * as XLSX from 'xlsx';

export default {
  name: 'MapChart',
  data() {
    return {
      chart: null,
      data: [],
      tag: null
    };
  },
  mounted() {
    this.initChart();
    console.log(this.$store);
  },
  computed: {
    globalday(){
      return this.$store.state.day
    },
    globaloption(){
      return this.$store.state.option;
    },
    globalcolor(){
      return this.$store.state.nowcolor;
    },
    globalcolor2(){
      return this.$store.state.depthcolor;
    },
    combinedData() {
    return {
      day: this.$store.state.day,
      hour: this.$store.state.hour,
      option: this.$store.state.option,
    };
  },
  },
  watch: {
  combinedData: {
    async handler(newVal, oldVal) {
      console.log("监控到变化");
      console.log("新值：", newVal);
      console.log("旧值：", oldVal);
      await this.loadExcelFromFile(newVal);
    },
    immediate: true, // 初始化时立即执行
  },
},
  methods: {
    async loadExcelFromFile(newVal) {
      console.log(newVal.option, "0");
      if (["气压", "温度", "相对湿度", "2分钟平均风速", "水平能见度"].includes(newVal.option)) {
        const fileName = `${process.env.BASE_URL}assets/weather_${newVal.day}_${newVal.hour?.split(":")[0]}.xlsx`;
        try {
          const response = await fetch(`${fileName}`);
          if (!response.ok) {
            throw new Error(`File not found at /assets/${fileName}`);
          }

          const data = await response.arrayBuffer();
          const workbook = XLSX.read(data, { type: 'array' });
          const sheetName = workbook.SheetNames[0];
          const sheet = workbook.Sheets[sheetName];
          const jsonData = XLSX.utils.sheet_to_json(sheet);

          // 提取省份和气压，并同时计算最大值和最小值
          let max = -Infinity;
          let min = Infinity;
          const mapData = jsonData.map((row) => {
            const pressure = row[this.globaloption];
            if (Number(pressure) > max) max = Number(pressure);
            if (Number(pressure) < min) min = Number(pressure);
            return {
              name: row['省份'],
              value: Number(pressure),
            };
          });

          console.log("最大气压:", max);
          console.log("最小气压:", min);
          console.log("最小气压:", mapData);

          // 调用更新方法，传递 map 数据
          this.updateWeatherData(mapData, max, min);

        } catch (error) {
          console.error('Error loading Excel file:', error);
          throw error;
        }
      }else{
        const fileName = `./assets/china_cities_2024${newVal.day.replace(/-/g, "")}.xlsx`;
        const response = await fetch(`${fileName}`);
          if (!response.ok) {
            throw new Error(`File not found at /assets/${fileName}`);
          }

        const data = await response.arrayBuffer();
        const workbook = XLSX.read(data, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const sheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);

        // 提取省份和气压，并同时计算最大值和最小值
        // let max = -Infinity;
        // let min = Infinity;
        // 从数据中提取 hour 为 0 时的 AQI 信息
        const result = jsonData.filter(row => row.hour === 0);
        let newData = [];
        let max = -Infinity;
        let min = Infinity;
        result.forEach(element => {
          if (element["type"] === newVal.option) {  // 判断条件
            // 使用 Object.entries() 遍历对象的键值对
            Object.entries(element).forEach(([key, value]) => {
              if (key !== "date" && key !== "hour" && key !== "type") {  // 判断条件
                newData.push({ "name": key, "value": value });  // 推入新数据
                if (value>max){
                  max = value;
                }
                if(value<min){
                  min=value;
                }
              }
            });
            return;
          }
        });
        console.log("最大气压:", max);
        console.log("最小气压:", min);

          // 调用更新方法，传递 map 数据
        this.updateWeatherData(newData, max, min);
      }
      
    },

    async initChart() {
      let max = -Infinity;
      let min = Infinity;
      const fileName = `./assets/weather_${this.globalday}_0.xlsx`;

      const response = await fetch(`${fileName}`);
        if (!response.ok) {
          throw new Error(`File not found at /assets/${fileName}`);
        }

        const data = await response.arrayBuffer();
        const workbook = XLSX.read(data, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const sheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);

        
        this.data = jsonData.map((row) => {
          const pressure = row[this.globaloption];
          if (Number(pressure) > max) max = Number(pressure);
          if (Number(pressure) < min) min = Number(pressure);
          return {
            name: row['省份'],
            value: Number(pressure),
          };
        });

      
      // 初始化 ECharts 实例
      this.chart = echarts.init(this.$refs.mapChart);

      // 注册地图
      echarts.registerMap('china', chinaJson);

      // 设置地图选项
      const option = {
        tooltip: {
          trigger: 'item',
        },
        visualMap: {
          min: min,
          max: max,
          left: 'right',
          top: '5%',
          itemWidth: 30,
          itemHeight: 200,
          text: ['高', '低'],
          calculable: true,
          inRange: {
            color: [this.globalcolor, this.globalcolor2],
          },
        },
        series: [
          {
            name: '中国地图',
            type: 'map',
            map: 'china',
            roam: false,
            label: {
              show: false,
            },
            data: this.data,
            itemStyle: {
              emphasis: {
                // 设置选中区域的颜色
                areaColor: this.globalcolor,
              },
            }
          },
        ],
      };

      this.chart.setOption(option);
      // 监听地图点击事件
      this.chart.on('click', (params) => {
        this.updateCity(params.name); // 使用箭头函数确保 this 指向 Vue 实例
      });
    },
    updateWeatherData(data, min, max) {
      if (!this.chart) {
        console.error("Chart instance is not initialized.");
        return;
      }
      this.chart.setOption({
        tooltip: {
          trigger: 'item',
        },
        visualMap: {
          min: min, // 动态更新最小值
          max: max, // 动态更新最大值
          left: 'right',
          top: '5%',
          itemWidth: 30,
          itemHeight: 200,
          text: ['高', '低'], // 文本显示
          calculable: true,
          inRange: {
            color: [this.globalcolor, this.globalcolor2], // 渐变颜色
          },
          outOfRange: {
            color: '#999', // 超出范围颜色
          },
        },
        series: [
          {
            name: '中国地图',
            type: 'map',
            map: 'china',
            roam: false,
            label: {
              show: false,
            },
            data: data, // 更新动态数据
            itemStyle: {
              emphasis: {
                // 设置选中区域的颜色
                areaColor: this.globalcolor, // 红色作为选中区域的颜色
              },
            }
          },
        ],
      }, true);

    },
    updateCity(city){
      this.$store.commit('setCity', city);
    }

  }
};

</script>

<style scoped>
/* 容器样式 */
.container {
  height: 80%; /* 确保容器高度占满视口 */
  width: 80%;
  display: flex;
  margin-left: auto; /* 容器靠右 */
}

/* 右侧内容样式 */
.right {
  width: 80%;
  height: 80%;
  margin: 20px; /* 增加间距 */
}
</style>