import { createStore } from 'vuex'

const store = createStore({
  state() {
    return {
      day: '10-30',
      hour: "0:00",
      option: "气压",
      secondcity: "四川",
      firstcity: "北京",
      nowcolor: "#FFD700", 
      depthcolor: "#7F6B00",
      city: "北京",
      option2: "气压",
      option3: "气压",
      info: "属暖温带半湿润气候区，四季分明，春秋短促，冬夏较长。夏季炎热多雨，冬季寒冷干燥，春、秋季气候较为宜人34",
      cities: ["北京", "天津"],
      flag: 1,
    }
  },
  mutations: {
    setDay(state, value) {
        state.day = value
    },
    setHour(state, value) {
        state.hour = value
    },
    setOption(state, value) {
        state.option = value
    },
    setFirstCity(state, value) {
        state.firstcity = value
    },
    setSecondCity(state, value) {
        state.secondcity = value
    },
    setColor(state, value) {
      state.nowcolor = value
    },
    setColor2(state, value) {
      state.depthcolor = value
    },
    setCity(state, value) {
      state.city = value
    },
    setOption2(state, value) {
      state.option2 = value
    },
    setInfo(state, value) {
      state.info = value
    },
    setCities(state, value) {
      state.cities = value
    },
    setFlag(state, value) {
      state.flag = value
    },
    setOption3(state, value) {
      state.option3 = value
    },
  }
})

export default store
