<template>
  <div>
    <!-- 导航栏 -->
    <nav class="navbar">
      <img
        src="../assets/太阳.png"
        class="time"
        @click.stop="toggleContent"
      >
      <div>
        <!-- 动态渲染日期按钮 -->
        <button
          v-for="(date, index) in dates"
          :key="index"
          class="day"
          :class="{'active': isActive(index)}"
          @click="setActive(index, $event)" 
        >
          &nbsp;&nbsp;&nbsp;{{ date }}&nbsp;&nbsp;&nbsp;
        </button>
      </div>
      <!-- 导航栏标题 -->
      <div class="navbar-title">
        风云变化图绘馆
      </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: "NavBar",
  data() {
    return {
      // 日期数组
      dates: ['10-30', '10-31', '11-01', '11-02', '11-03', '11-04'],
      // 默认选中第一个日期按钮
      activeIndex: 0,  
    };
  },
  methods: {
    // 判断当前按钮是否被选中
    isActive(index) {
      return this.activeIndex === index;
    },
    // 设置被点击的按钮索引并获取按钮文本
    setActive(index, event) {
      this.activeIndex = index;
      
      // 获取按钮的文本内容
      let text = event.target.innerText;
      
      // 清理文本，去除空格
      text = text.replace(/\s+/g, ' ').trim();
      this.$store.commit('setDay', text);
      console.log(text);  // 输出按钮的文本内容
    },
    // 处理太阳图标的点击事件（例如：展开/收起某些内容）
    toggleContent() {
      console.log("太阳图标被点击");  // 你可以在这里执行更多的操作
      // 比如你可以切换某个内容的显示与隐藏
    }
  }
};
</script>

<style scoped>
/* 导航栏样式 */
.navbar {
  position: fixed;
  z-index: 1000; /* 确保导航栏在最上层 */
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  height: 60px; /* 导航栏高度 */
  align-items: center; /* 垂直居中 */
  background-color: #92d8d5;
}

/* 太阳图片样式 */
.time {
  height: 40px; /* 图片大小 */
  width: auto;
  margin-left: 1rem;
  cursor: pointer; /* 鼠标悬浮显示为可点击 */
}

/* 导航栏标题样式 */
.navbar-title {
  font-size: 1.5rem;
  font-weight: bold;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
}

/* 日期按钮样式 */
.day {
  padding: 5px; /* 缩小按钮大小 */
  cursor: pointer;
  border: none;
  background-color: transparent;
  transition: all 0.3s ease;
  font-size: 1.1rem; /* 增加文字大小 */
}

/* 被选中的按钮 */
.day.active {
  color: white;
  transform: scale(1.2); /* 变大 */
}
</style>

<style>
/* 全局样式 */
html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
</style>
