<template>
  <el-timeline class="tag">
    <el-timeline-item
      v-for="(activity, index) in activities"
      :key="index"
      class="custom-timeline-item"
      :class="{ active: activity.active }"
      @click.stop="toggleActive(index)"
    >
      <span class="timeline-text"  style="color: black" >
        {{ activity.content }}
      </span>
    </el-timeline-item>
  </el-timeline>
</template>

<script>
export default {
  name: "TagSelect",
  data() {
    return {
      activities: [
        { content: "气压", active: true, color: "#B3C8CF" },  
        { content: "温度", active: false, color: "#C62E2E" }, 
        // #81BFDA
        { content: "相对湿度", active: false, color: "#81BFDA" }, 
        { content: "2分钟平均风速", active: false, color: "#85A98F" }, 
        { content: "水平能见度", active: false, color: "#F6F7C4" }, 
        { content: "AQI", active: false, color: "#DEAA79" }, 
        { content: "PM2.5", active: false, color: "#C1BAA1" }, 
        { content: "PM10", active: false, color: "#A59D84" }, 
        { content: "CO", active: false, color: "#8EA3A6" }, 
        { content: "NO2", active: false, color: "#9AA6B2" }, 
        { content: "SO2", active: false, color: "#66785F" }, 
      ]

    };
  },
  methods: {
    toggleActive(index) {
      // 将所有活动状态重置为 false
      this.activities.forEach((activity) => (activity.active = false));
      // 仅激活当前点击的标签
      this.activities[index].active = true;
      this.$store.commit('setOption', this.activities[index].content);
      this.$store.commit('setColor', this.activities[index].color);
      this.$store.commit('setColor2', this.darkenHexColor(this.activities[index].color, 50));
    },
    darkenHexColor(hex, percent) {
      if (!/^#[0-9A-Fa-f]{6}$/i.test(hex)) {
        throw new Error("Invalid hex color format");
      }

      // 去掉 #，获取RGB值
      hex = hex.slice(1);
      
      // 将RGB转换为十进制数值
      let r = parseInt(hex.slice(0, 2), 16);
      let g = parseInt(hex.slice(2, 4), 16);
      let b = parseInt(hex.slice(4, 6), 16);
      
      // 计算每个通道的新值
      r = Math.max(0, Math.min(255, r - (r * percent / 100)));
      g = Math.max(0, Math.min(255, g - (g * percent / 100)));
      b = Math.max(0, Math.min(255, b - (b * percent / 100)));
      
      // 将新的RGB值转换回16进制颜色
      const darkenedHex = `#${(1 << 24 | (r << 16) | (g << 8) | b).toString(16).slice(1).toUpperCase()}`;
      
      return darkenedHex;
    }
  },
};
</script>

<style scoped>
.tag {
  width: 15%;
}
/* 调整 el-timeline-item 的整体间距 */
.custom-timeline-item {
  margin-bottom: 10px;
  cursor: pointer;
  top: 5%;
}

/* 调整时间点和文字的对齐方式 */
.custom-timeline-item .el-timeline-item__wrapper {
  display: flex;
  text-align: left; /* 设置文本左对齐 */
}

/* 默认时间点样式 */
.custom-timeline-item .el-timeline-item__node {
  width: 10px;
  height: 10px;
  margin-left: 5px;
  transition: background-color 0.3s ease; /* 平滑过渡效果 */
}

/* 默认文字样式 */
.timeline-text {
  font-size: 20px; /* 默认字体大小 */
  transition: color 0.3s ease, font-size 0.3s ease; /* 添加平滑过渡动画 */
  text-align: left; /* 设置文本左对齐 */
}

/* 激活状态的样式 */
.custom-timeline-item.active .timeline-text {
  font-size: 25px; /* 激活状态文字字体大小 */
}

/* 激活状态时间点的样式 */
.custom-timeline-item.active .el-timeline-item__node {
  width: 15px;
  height: 15px;
}
</style>
