<template>
    <div class="container">
      <div class="buttons-container">
        <!-- 渲染每个下拉框 -->
        <div v-for="(province, index) in provinces" :key="index" class="dropdown-wrapper">
          <select v-model="provinces[index]" class="province-select" @change="loadData">
            <option v-for="option in allProvinces" :key="option" :value="option">
              {{ option }}
            </option>
          </select>
          <button @click="removeProvince(index)" class="remove-button">删除</button>
        </div>
      </div>
  
      <!-- 添加下拉框按钮 -->
      <button @click="addProvince" class="add-button" :disabled="provinces.length >= 5">+</button>
      <div class="option3"><CitySelect2/></div>
    </div>
  </template>
  
  <script>
  import CitySelect2 from './CitySelect2';
  export default {
    data() {
      return {
        // 所有可选择的省份
        allProvinces: [
          '北京', '天津', '上海', '重庆', '河北', '山西', '辽宁', '吉林', '黑龙江', 
          '江苏', '浙江', '安徽', '福建', '江西', '山东', '河南', '湖北', '湖南', 
          '广东', '海南', '四川', '贵州', '云南', '陕西', '甘肃', '青海', '台湾', 
          '内蒙古', '广西', '西藏', '宁夏', '新疆', '香港', '澳门'
        ],
        provinces: ["北京", "天津"],
      };
    },
    components:{
        CitySelect2,
    },
    computed:{
        globalFlag(){
            return this.$store.state.flag;
        }
    },
    methods: {
      // 增加一个新的下拉框
      addProvince() {
        if (this.provinces.length < 4) { // 确保最多只有五个下拉框
          this.provinces.push('新省份'); // 默认添加一个“新省份”
          this.$store.commit('setCities', this.provinces);
          if (this.globalFlag==1){
            this.$store.commit('setFlag', 0);
          }else{
            this.$store.commit('setFlag', 1);
          }
        }
      },
      // 删除指定的下拉框
      removeProvince(index) {
        this.provinces.splice(index, 1);
        this.$store.commit('setCities', this.provinces);
        if (this.globalFlag==1){
            this.$store.commit('setFlag', 0);
          }else{
            this.$store.commit('setFlag', 1);
          }
      },
      loadData(){
        console.log(this.provinces);
        this.$store.commit('setCities', this.provinces);
        if (this.globalFlag==1){
            this.$store.commit('setFlag', 0);
        }else{
            this.$store.commit('setFlag', 1);
        }
      }
    }
  };
  </script>
  
  <style scoped>
  .container {
    /* background-color: #f2cccc; */
    width: 100%;
    padding: 10px;
    display: flex;
  }
  
  .buttons-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }
  
  .dropdown-wrapper {
    display: flex;
    align-items: center;
  }
  
  .province-select {
   height: 50px;
    width: 150px;
    background-color: #F9DADA;
    font-size: 18px;
    color: #0f0e0e;
    margin-left: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-bottom: 0px;
    
  }
  
  .remove-button {
    margin-left: 10px;
    padding: 5px 10px;
    background-color: #f2cccc;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-button {
    margin-top: 20px;
    padding: 10px 20px;
    background-color: #f2cccc;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 15px;
  }
  
  .add-button:disabled {
    background-color: #ddd; /* 禁用时的颜色 */
    cursor: not-allowed; /* 禁用时光标 */
  }
  
  .add-button:hover,
  .remove-button:hover,
  .province-select:hover {
    opacity: 0.8;
  }
  .option3{
    margin-left: auto; /* 容器靠右 */

  }
  </style>
  